/* buttons */
button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer; }
  button:focus {
    outline: none; }

.btn {
  min-height: 48.4px;
  padding: 12px 75px 8px 38.9px;
  font-size: 1.125rem;
  font-weight: bold;
  text-transform: uppercase;
  text-decoration: none;
  color: #232323;
  display: inline-block;
  background: transparent;
  position: relative; }
  .btn::before, .btn::after {
    content: '';
    position: absolute;
    top: 0;
    right: 10px;
    bottom: 0;
    width: 157px;
    transform: skewX(25deg);
    transition: ease-in-out 0.25s;
    background: #fff648; }
  .btn::after {
    background: #f0f5fa;
    display: none; }
  .btn > span {
    position: relative;
    z-index: 5;
    white-space: nowrap; }
    .btn > span::after, .btn > span::before {
      content: '';
      position: absolute;
      right: -40px;
      bottom: 5px;
      background: #232323; }
    .btn > span::after {
      width: 26px;
      height: 2px; }
    .btn > span::before {
      width: 2px;
      height: 13px;
      transform: rotate(-20deg); }
  .btn:hover, .btn:focus {
    color: #232323;
    text-decoration: none; }
    .btn:hover::before, .btn:focus::before {
      width: calc(100% - 20px); }
  .btn--send {
    transform: translateX(-25px); }
    .btn--send::before, .btn--send::after {
      width: 125px; }
      @media (min-width: 768px) {
        .btn--send::before, .btn--send::after {
          width: 160px; } }
  .btn--register {
    min-height: 39px;
    padding: 10px 42px 8px 35px;
    font-size: 0.875rem; }
    .btn--register::before {
      width: calc(100% - 20px);
      z-index: 2; }
    .btn--register::after {
      width: calc(100% - 36px);
      display: block;
      transform: skewX(25deg) rotate(-1.5deg);
      transform-origin: 100% 100%;
      z-index: 1; }
    .btn--register span {
      opacity: 1;
      transition: all 0.35s;
      white-space: nowrap; }
      .btn--register span::before {
        display: none; }
      .btn--register span::after {
        height: 0;
        width: 0;
        border-left: 17px solid #a59d06;
        border-bottom: 5px solid transparent;
        bottom: -15px;
        right: -41px;
        background: transparent;
        opacity: 1;
        transition: all 0.35s; }
    .btn--register:hover::after {
      transform: skewX(25deg) rotate(0deg); }
    .btn--register:hover span {
      opacity: 0.7; }
  .btn--only-arrow {
    padding: 0;
    width: 60px; }
    .btn--only-arrow::before {
      width: 60px; }
    .btn--only-arrow span::after {
      width: 18px; }
    .btn--only-arrow span::after, .btn--only-arrow span::before {
      right: 4px; }
  .btn--more span::after {
    display: none; }
  .btn--more span::before {
